@import '../../variables';

@each $theme-color, $v in $theme-colors {
  .#{$theme-color} {
      .todoCheckbox input[type="checkbox"] {
          background-color: theme-color($theme-color, 'background-secondary');
      }
      .todoCheckbox {
        background-color: theme-color($theme-color, 'checkbox');
      }
  }
}

.todoCheckbox {
    display: flex;
    width: 25px;
    height: 25px;
    margin-top: 10px;
    margin-left: 10px;
    border-radius: 50%;
    -webkit-transition: all 0.5s 0s ease-in-out;
    -moz-transition: all 0.5s 0s ease-in-out;
    -o-transition: all 0.5s 0s ease-in-out;
    transition: all 0.5s 0s ease-in-out;
    float: left;
    cursor: pointer;
    overflow: hidden;
    position: relative;
    justify-content: center;
    align-items: center;
  }

.todoCheckbox:hover, .todoCheckbox.completed {
  background: rgb(132,202,254);
  background: linear-gradient(-225deg, hsl(192, 100%, 67%) 0%, hsl(280, 87%, 65%) 100%);
}

.todoCheckbox img {
  position: absolute;
  z-index: 2;
  margin: auto;
  pointer-events: none;
}

.todoCheckbox input[type="checkbox"] {
  appearance: none;
  width: 23px;
  height: 23px;
  position: absolute;
  border-radius: 50%;
  z-index: 1;
  margin: auto;
  cursor: pointer;
  transition: all 300ms ease-in;
}

.todoCheckbox.completed input[type="checkbox"] {
  opacity: 0;
}

.todoCheckbox.disabled {
  pointer-events: none;
  cursor: none;
}
